import request from '../utils/request'
import apiPath from './url'
import qs from 'qs'

export function getList (data) {
  data = qs.stringify(data)
  return request({
    url: apiPath.search.getList,
    method: 'post',
    data: data
  })
}
export function getDetail (data) {
  data = qs.stringify(data)
  return request({
    url: apiPath.technology.getDetail,
    method: 'post',
    data: data
  })
}
