<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                <input class="search-input" v-model="searchParams.keyword" type="text" placeholder="请输入关键字">
                <button class="search-btn" @click="search">搜索</button>
            </p>
        </section>
        <md-tab-bar
                v-if="isTabShow"
                v-model="current"
                :items="items"
                :maxLength="5"
                @change="onTabChange"
        />
        <md-scroll-view v-if="isTabShow" :style="{height: Height}" @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
            <div
                    v-for="i in listResult"
                    class="scroll-view-item"
                    :key="i.id"
                    @click="$_onItemClick(i)">
<!--                <img :src="i.coverPicture" alt="">-->
                <img src="../../assets/video/details_image_02@2x.png" alt="">
                <div class="article">
                    <p v-text="i.title"></p>
                    <span><img src="@/assets/home/icon_time@2x.png">{{ i.createTime }}</span>
                </div>
            </div>
            <md-scroll-view-more slot="more" :is-finished="isFinished">
            </md-scroll-view-more>
        </md-scroll-view>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/search'
import { TabBar, Swiper, SwiperItem, ScrollView, ScrollViewMore, DropMenu, Icon, Toast, TabPicker } from 'mand-mobile'
export default {
  name: 'search',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [ScrollViewMore.name]: ScrollViewMore,
    [DropMenu.name]: DropMenu,
    [Icon.name]: Icon,
    [Toast.name]: Toast,
    [TabPicker.name]: TabPicker
  },
  data () {
    return {
      searchParams: {
        pageSize: 10,
        pageNumber: 1,
        keyword: ''
      },
      // 是否展示tab切换
      isTabShow: false,
      // 当前选中tab
      current: 1,
      currentDetailUrl: '',
      // tab项
      items: [
        { name: 1, label: '需求' },
        { name: 2, label: '成果' },
        { name: 3, label: '活动' },
        { name: 4, label: '设备' },
        { name: 5, label: '专家' },
        { name: 6, label: '单位' },
        { name: 7, label: '项目' },
        { name: 8, label: '资讯' },
        { name: 9, label: '案例' },
        { name: 10, label: '视频' }
      ],
      typeIds: [
        { typeId: 1, label: '平台咨询', name: 8, url: '/news/detail' },
        { typeId: 2, label: '行业咨询', name: 8, url: '/news/detail' },
        { typeId: 3, label: '活动通知', name: 3, url: '/activity/detail' },
        { typeId: 4, label: '项目申报', name: 7, url: '/project/detail' },
        { typeId: 5, label: '视频展示', name: 10, url: '/video/detail' },
        { typeId: 6, label: '典型案例', name: 9, url: '/case/detail' },
        { typeId: 7, label: '基础信息', name: 8, url: '/news/detail' },
        { typeId: 8, label: '政策', name: 8, url: '/news/detail' },
        { typeId: 9, label: '国家政策', name: 8, url: '/news/detail' },
        { typeId: 10, label: '地方政策', name: 8, url: '/news/detail' },
        { typeId: 11, label: '政策解读', name: 8, url: '/news/detail' },
        { typeId: 12, label: '军民指南', name: 8, url: '/news/detail' },
        { typeId: 13, label: '活动', name: 3, url: '/activity/detail' },
        { typeId: 14, label: '技术对接', name: 1, url: '/technology/detail' },
        { typeId: 15, label: '成果转化', name: 2, url: '' },
        { typeId: 16, label: '设备共享', name: 4, url: '/device/detail' }
      ],
      // tab项
      list: [],
      listResult: [],
      isFinished: false,
      Height: '900px',
      MenuData: [
        {
          text: '所属领域',
          options: []
        }
      ],
      show: false
    }
  },
  mounted () {
    this.Height = window.innerHeight - 100 + 'px'
  },
  methods: {
    initData () {
      // 获取当前tab的typeId
      let currentTypeId = ''
      this.typeIds.forEach(item => {
        if (item.name === this.current) {
          currentTypeId = item.typeId
          this.currentDetailUrl = item.url
        }
      })
      console.log('currentTypeId:' + currentTypeId)
      // 过滤符合条件的数据
      this.listResult = []
      this.list.forEach((item, key) => {
        if (item.typeId === currentTypeId) {
          this.listResult.push(item)
        }
      })
    },
    search () {
      getList(this.searchParams).then(res => {
        if (res.data.length > 0) {
          this.isTabShow = true
        }
        this.list = res.data
        this.initData()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onTabChange (item, index) {
      this.searchParams.pageNumber = 1
      this.current = item.name
      this.initData()
    },
    getSelectedValue (index) {
      console.log(index)
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      console.log(i)
      this.$router.push({
        path: this.currentDetailUrl,
        query: { id: i.id }
      })
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.list = this.list.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    },
    // 跳转关键字检索页
    goSearch () {
      this.$router.push('/search/index')
    },
    changeMenu (val) {
      this.searchParams.genre = this.$refs.menu.getSelectedValue(0).value
      this.getList()
    },
    showMenu (val) {
      this.show = true
    },
    handleChange (val) {
      val.values.forEach((item, key) => {
        switch (key) {
          case 0: this.searchParams.province = item; break
          case 1: this.searchParams.city = item; break
          case 2: this.searchParams.area = item; break
        }
      })
      this.getList()
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        .title
            background-color #EEEEEEFF!important
            .go-back
                color #888888FF
            .search-input
                border 0
                width 514px
                height 50px
                border-radius 25px
                font-size 24px
                padding-left 30px
                outline none
                margin-left 70px
            .search-btn
                width 110px
                height 50px
                background linear-gradient(0deg,rgba(2,25,85,1),rgba(2,85,149,1))
                border-radius 25px
                font-size 24px
                font-family PingFang-SC-Medium
                font-weight 500
                color rgba(255,255,255,1)
                border 0
                margin-left 20px
    /deep/.md-tab-bar
                margin-top 48px
                background-color #ffffff
                border-bottom 1px solid rgba(181,181,181,1)
                .md-tab-bar-item
                    color #999999FF!important
                    font-size 28px
                    font-weight 500
                    min-height 80px!important
                    padding 0 44px!important
                .md-tab-bar-item.is-active
                    color #021955FF!important
                .md-tab-bar-ink
                    background-color #021955FF
    .md-scroll-view
        padding 20px
    .scroll-view-container
        div
            display flex
            justify-content space-between
            .article
                width 4.1rem
                text-align left
                margin 0.1rem 0
                display flex
                flex-direction column
                justify-content space-between
                span img
                    margin-right 10px
                    width 24px
                p
                    color #333
                    font-size 0.28rem
                    font-weight Medium
                    text-overflow ellipsis
                    overflow hidden
                    display -webkit-box
                    -webkit-line-clamp 2
                    -webkit-box-orient vertical
    .scroll-view-item
        text-align center
        font-size 0.28rem
        font-family DINAlternate-Bold
        height 150px
        margin-bottom 0.3rem
        border 2px solid rgba(153,153,153,0.2)
        border-radius 4px
        background-color #F9FBFFFF
        label
            position absolute
            color #fff
            background linear-gradient(0deg, #ca0239, #ff6135)
            height 0.32rem
            line-height 0.32rem
            left 0.08rem
            font-size 0.2rem
            font-family PingFang-SC-Regular
            border-radius: 0 0 4px 4px
        img
            width 260px
    .scroll-view-container .md-scroll-view-more
        justify-content center
</style>
